/* Saved Page Styles */

.saved-page {
  -webkit-animation: fadeIn 0.3s ease;
          animation: fadeIn 0.3s ease;
}

@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
  }
  to {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
  }
  to {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

/* Stats Bar */
.stats-bar {
  display: -ms-flexbox;
  display: flex;
  gap: var(--spacing-lg);
  padding: var(--spacing-md) var(--spacing-lg);
  background: var(--bg-card);
  border-radius: var(--radius-lg);
  border: 1px solid var(--border-color);
  margin-bottom: var(--spacing-xl);
}

.stat-item {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  gap: var(--spacing-sm);
  color: var(--text-secondary);
  font-size: 0.9375rem;
}

.stat-item svg {
  color: var(--primary-light);
}

.stat-item strong {
  color: var(--text-primary);
}

/* Saved article cards */
.saved-article-card {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.article-card-content {
  -ms-flex: 1 1;
      flex: 1 1;
  margin-bottom: var(--spacing-md);
}

.saved-article-card .article-title {
  display: block;
  margin-bottom: var(--spacing-sm);
}

.saved-article-card .article-actions {
  margin-top: auto;
  padding-top: var(--spacing-md);
  border-top: 1px solid var(--border-color);
}

/* Delete confirmation */
.delete-confirm {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  gap: var(--spacing-sm);
}

.delete-confirm-text {
  color: var(--danger);
  font-size: 0.875rem;
  font-weight: 500;
}

/* Articles grid */
.articles-grid {
  display: grid;
  gap: var(--spacing-md);
}

@media (min-width: 768px) {
  .articles-grid {
    gap: var(--spacing-lg);
  }
}

@media (min-width: 768px) {
  .articles-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (min-width: 1200px) {
  .articles-grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

/* Skeleton loading */
.skeleton-card {
  pointer-events: none;
}

.skeleton-card:hover {
  -webkit-transform: none;
      -ms-transform: none;
          transform: none;
  border-color: var(--border-color);
  -webkit-box-shadow: var(--shadow-md);
          box-shadow: var(--shadow-md);
}

.skeleton-title {
  height: 24px;
  width: 80%;
  margin-bottom: var(--spacing-md);
}

.skeleton-meta {
  height: 16px;
  width: 40%;
  margin-bottom: var(--spacing-md);
}

.skeleton-text {
  height: 14px;
  width: 60%;
}

/* Empty state */
.empty-state {
  margin-top: var(--spacing-xl);
}

.empty-state-icon {
  color: var(--primary);
}

/* Mobile optimizations */
@media (max-width: 767px) {
  .saved-article-card {
    padding: var(--spacing-md);
  }

  .article-actions .btn {
    -ms-flex: 1 1;
        flex: 1 1;
    -ms-flex-pack: center;
        justify-content: center;
  }

  .delete-confirm {
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: end;
        justify-content: flex-end;
  }

  .delete-confirm-text {
    width: 100%;
    text-align: right;
    margin-bottom: var(--spacing-xs);
  }
}



