/* Top Navigation Bar */
.top-navbar {
  background: rgba(15, 23, 42, 0.95);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border-bottom: 1px solid var(--border-color);
  padding: var(--spacing-md) var(--spacing-lg);
  position: sticky;
  top: 0;
  z-index: 1000;
}

.navbar-container {
  max-width: 1400px;
  margin: 0 auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.navbar-brand {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  gap: var(--spacing-sm);
  text-decoration: none;
  -webkit-transition: -webkit-transform var(--transition-fast);
  transition: -webkit-transform var(--transition-fast);
  -o-transition: transform var(--transition-fast);
  transition: transform var(--transition-fast);
  transition: transform var(--transition-fast), -webkit-transform var(--transition-fast);
}

.navbar-brand:hover {
  -webkit-transform: scale(1.02);
      -ms-transform: scale(1.02);
          transform: scale(1.02);
}

.brand-logo {
  height: 40px;
  width: auto;
  display: block;
}

@media (min-width: 768px) {
  .brand-logo {
    height: 50px;
  }
}

.navbar-links {
  display: none;
  gap: var(--spacing-sm);
}

@media (min-width: 768px) {
  .navbar-links {
    display: -ms-flexbox;
    display: flex;
  }
}

.nav-link {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  gap: var(--spacing-sm);
  padding: var(--spacing-sm) var(--spacing-md);
  color: var(--text-secondary);
  font-weight: 500;
  font-size: 0.9375rem;
  border-radius: var(--radius-md);
  text-decoration: none;
  -webkit-transition: all var(--transition-fast);
  -o-transition: all var(--transition-fast);
  transition: all var(--transition-fast);
}

.nav-link:hover {
  color: var(--text-primary);
  background: var(--bg-card);
}

.nav-link.active {
  color: var(--primary-light);
  background: rgba(99, 102, 241, 0.1);
}

.nav-icon {
  width: 18px;
  height: 18px;
}

/* Mobile Bottom Navigation */
.mobile-nav {
  display: -ms-flexbox;
  display: flex;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(15, 23, 42, 0.98);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border-top: 1px solid var(--border-color);
  padding: var(--spacing-sm);
  padding-bottom: calc(var(--spacing-sm) + var(--safe-area-bottom));
  z-index: 1000;
}

@media (min-width: 768px) {
  .mobile-nav {
    display: none;
  }
}

.mobile-nav-item {
  -ms-flex: 1 1;
      flex: 1 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  gap: 2px;
  padding: var(--spacing-sm);
  color: var(--text-muted);
  text-decoration: none;
  font-size: 0.75rem;
  font-weight: 500;
  border-radius: var(--radius-md);
  -webkit-transition: all var(--transition-fast);
  -o-transition: all var(--transition-fast);
  transition: all var(--transition-fast);
}

.mobile-nav-item:hover,
.mobile-nav-item.active {
  color: var(--primary-light);
  background: rgba(99, 102, 241, 0.1);
}

.mobile-nav-icon {
  width: 24px;
  height: 24px;
}



