/* 404 Not Found Page Styles */

.not-found-page {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  min-height: 60vh;
  text-align: center;
  -webkit-animation: fadeIn 0.3s ease;
          animation: fadeIn 0.3s ease;
}

@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
  }
  to {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
  }
  to {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

.not-found-content {
  max-width: 500px;
  padding: var(--spacing-xl);
}

.not-found-code {
  font-size: clamp(6rem, 20vw, 10rem);
  font-weight: 800;
  line-height: 1;
  background: -webkit-linear-gradient(315deg, var(--primary) 0%, var(--primary-light) 50%, var(--accent) 100%);
  background: -o-linear-gradient(315deg, var(--primary) 0%, var(--primary-light) 50%, var(--accent) 100%);
  background: linear-gradient(135deg, var(--primary) 0%, var(--primary-light) 50%, var(--accent) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin-bottom: var(--spacing-md);
  -webkit-animation: pulse 2s ease-in-out infinite;
          animation: pulse 2s ease-in-out infinite;
}

@-webkit-keyframes pulse {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.7;
  }
}

@keyframes pulse {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.7;
  }
}

.not-found-content h1 {
  font-size: clamp(1.5rem, 5vw, 2rem);
  margin-bottom: var(--spacing-sm);
}

.not-found-content p {
  color: var(--text-secondary);
  font-size: 1.125rem;
  margin-bottom: var(--spacing-xl);
}

.not-found-actions {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  gap: var(--spacing-md);
}

@media (min-width: 480px) {
  .not-found-actions {
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: center;
        justify-content: center;
  }
}



